<template>
  <div id="wrap" class="renting">
    <div class="header">
      <span class="type">短租</span>
      <span class="car">{{car_notice_number}}</span>
    </div>
    <div class="input-content">
      <div>
        <span>租赁车辆数（台）</span>
        <input type="tel" v-model="num">
      </div>
      <div>
        <span>租赁周期（1周期3个月）</span>
        <input type="tel" v-model="day">
      </div>
    </div>
    <div class="offer-content" style="margin-bottom: 100px;">
      <div class="offer-wrap">
        <span class="project bolder">报价项目</span>
        <span class="price bolder">单价</span>
        <span class="sum bolder">合计</span>
      </div>
      <div class="offer-wrap">
        <span class="project">短租车辆保证金（元/辆）</span>
        <span class="price">{{car_assure_price_short}}</span>
        <span class="sum">{{car_assure_price_short_sum}}</span>
      </div>
      <div class="offer-wrap">
        <span class="project">车载设备保证金（元/辆）</span>
        <span class="price">{{car_device_assure_price}}</span>
        <span class="sum">{{car_device_assure_price_sum}}</span>
      </div>
      <div class="offer-wrap">
        <span class="project">短租交通违法保证金（元/辆）</span>
        <span class="price">{{traffic_assure_price_short}}</span>
        <span class="sum">{{traffic_assure_price_short_sum}}</span>
      </div>
      <div class="offer-wrap">
        <span class="project">运营保证金（元/辆）</span>
        <span class="price">{{operate_assure_price}}</span>
        <span class="sum">{{operate_assure_price_sum}}</span>
      </div>
      <div class="offer-wrap">
        <span class="project">车辆技改（元/辆）</span>
        <span class="price">{{vehicle}}</span>
        <span class="sum">{{Vehicle_sum}}</span>
      </div>
      <!-- <div class="offer-wrap">
        <span class="project">首付金额（元）</span>
        <span class="price">{{down_money}}</span>
        <span class="sum">{{down_money_sum}}</span>
      </div> -->
      <div class="offer-wrap">
        <span class="project">车辆租金（元/月/辆）</span>
        <span class="price">{{car_price}}</span>
        <span class="sum">{{car_price_sum}}</span>
      </div>
    </div>
    <div class="footer-btn">
      <mt-checklist
        v-model="value"
        :options="options"
        ref="nearby_price">
      </mt-checklist>
      <mt-button class="nextBtn" type="primary" @click="count">计算</mt-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      car_notice_number: '',
      car_assure_price_short: '',
      car_assure_price_short_sum: '',
      car_device_assure_price: '',
      car_device_assure_price_sum: '',
      traffic_assure_price_short: '',
      traffic_assure_price_short_sum: '',
      operate_assure_price: '',
      operate_assure_price_sum: '',
      vehicle: '',
      Vehicle_sum: '',
      car_price: 0,
      car_price_sum: '',
      down_money: 0,
      down_money_sum: '',
      num: 0,
      day: 0,
      value: [],
      options: [{
        label: '是否周边城市报价',
        value: '200'
      }],
      selectData: {}
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      document.title = '在线报价'
    })
  },
  computed: {
    getCarInfo() {
      return this.$store.getters['GET_CAR_INFO']
    },
    getServiceInfo() {
      return this.$store.getters['GET_SERVICE_INFO']
    },
    getVehicle() {
      return this.$store.getters['GET_VEHICLE_INFO']
    }
  },
  mounted() {
    this.car_notice_number = this.getCarInfo.car_notice_number
    this.car_assure_price_short = this.getCarInfo.car_assure_price_short
    this.car_device_assure_price = this.getCarInfo.car_device_assure_price
    this.traffic_assure_price_short = this.getCarInfo.traffic_assure_price_short
    this.operate_assure_price = this.getCarInfo.operate_assure_price
    this.vehicle = this.getVehicle.price === undefined ? 0 : this.getVehicle.price
    this.selectData.main_id = this.getCarInfo.id
    this.selectData.price_mode = this.getServiceInfo.choosePriceMode
    this.selectData.service_price = this.getServiceInfo.price
  },
  methods: {
    count() {
      let num = this.num
      let that = this
      this.car_assure_price_short_sum = this.car_assure_price_short * num
      this.car_device_assure_price_sum = this.car_device_assure_price * num
      this.traffic_assure_price_short_sum = this.traffic_assure_price_short * num
      this.operate_assure_price_sum = this.operate_assure_price * num
      this.Vehicle_sum = this.vehicle * num
      this.selectData.nearby_price = this.$refs.nearby_price.value
      this.selectData.cycle_num = this.day
      this.$axios({
        url: this.$baseUrl + '?r=interfaces/car-valuation/get-car-price',
        method: 'post',
        data: this.selectData,
        transformRequest: [function (data) {
          var ret = '';
          for (var it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        if (res.data.errcode === 0) {
          that.car_price = res.data.data.car_price
          that.car_price_sum = that.car_price * num
          that.down_money = res.data.data.down_payment
          that.down_money_sum = that.down_money * num
        } else {
          that.$toast(res.data.msg)
        }
      })
    }
  }
}
</script>




